body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Tweet CSS */
.tweet {
    width:40%;
    border:1px solid blue;
    border-radius: 4px;
    margin-left:auto;
    margin-right:auto;
    margin-bottom:20px;
    padding:20px;
    text-align:left;
}

.tweet .date {
    opacity:.3;
    font-size:.8em;
}

.tweet .likes {
    border-top:1px solid #d3d3d3;
    opacity:.3;
    cursor: pointer;
}
input.form-control {
    margin-bottom: 20px;
    margin-top:5px;
}

.tweetContainer {
    text-align: center;
}
